import React from "react";
import styled, { css } from "styled-components";

const StyledLogo = styled.h2`
  color: #9cd5c2;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  background: none;
  border: none;
  background: #fff;
  border-radius: 20rem;
  padding: 2px 20px;
  :hover {
    color: #9cd5c2;
  }
`;

function Logo({ children, ...props }) {
  return <StyledLogo {...props}>{children}</StyledLogo>;
}
export default Logo;
