import React from "react";
import styled, { css } from "styled-components";

const StyledBold = styled.b`
  font-size: 22px;
  font-weight: 600;
  line-height: 1.5;
  color: #9cd5c2;
  ${(props) =>
    props.large &&
    css`
      font-size: 30px;
    `}
`;

function Bold({ children, ...props }) {
  return <StyledBold {...props}>{children}</StyledBold>;
}
export default Bold;
