import React from "react";
import styled, { css } from "styled-components";
import Tag from "./Tag";

const StyledTags = styled.div`
  width: 60vw;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
`;

function Tags({ children, ...props }) {
  return (
    <div>
      <StyledTags {...props}>
        <Tag value=""></Tag>
        <Tag value=""></Tag>
        <Tag value=""></Tag>
        <Tag value=""></Tag>
        <Tag value=""></Tag>
      </StyledTags>
    </div>
  );
}
export default Tags;
