import React from "react";
import styled, { css } from "styled-components";
import { useNavigate } from "react-router-dom";

const StyledTag = styled.button`
  width: 100%;
  background: #fff;
  border: 2px solid #9cd5c2;
  color: #9cd5c2;
  border-radius: 0.6rem;
  font-size: 0.6rem;
  padding: 0.5em 1rem;
  margin: 0.5em;
  cursor: pointer;
  :hover {
    background: #9cd5c2;
    color: #fff;
  }
`;

function Tag({ children, ...props }) {
  const navigate = useNavigate();
  function TagClick() {
    console.log(props.value);
    window.localStorage.removeItem("searchValue");
    window.localStorage.setItem("tag", props.value);
    navigate("/map");
  }
  return (
    <StyledTag onClick={TagClick} {...props}>
      {children}
    </StyledTag>
  );
}
export default Tag;
