import React from "react";
import styled, { css } from "styled-components";

const StyledSelect = styled.select`
  background: #fff;
  border: 2px solid #9cd5c2;
  color: #9cd5c2;
  border-radius: 1rem;
  font-size: 17px;
  padding: 7px 0;
  cursor: pointer;
  outline: none;
  text-align: center;
  option {
    font-size: 17px;
    cursor: pointer;
  }
`;

function Select({ children, ...props }) {
  function selectEvent(e) {
    // console.log(e.target.value);
    localStorage.setItem("tag", e.target.value);
  }
  return (
    <StyledSelect onChange={selectEvent} id="select">
      <option value="" selected disabled>
         
      </option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </StyledSelect>
  );
}
export default Select;
