import React from "react";
import Slider from "react-slick";
// import './S3slidecardMypage.scss';
import SlideText from './SlideText';
import styled from "styled-components";


const CardWrapper = styled.div`
width:90%;
margin:0 auto;
  display:flex;
  flex-direction:row;
`;

class Slide extends React.Component {
  render() {
    var settings = {
      //page
      dots: true,
      // 
      infinite: true,
      //
      arrows : true,
      speed: 500,
      //       ( 1 )
      slidesToShow: 1,
      //       
      slidesToScroll: 1,
      // 
      autoplay: true,
      //   ? 
      autoplaySpeed: 3500,
      //      
      pauseOnHover: true,
      
    };
    return (
      <Slider {...settings} className="Slider" style={{
        width:'66%' ,
        
        margin:'0 auto'
      }}>
        <div className="wrapper">
          <CardWrapper>
            <SlideText />
            <SlideText />
            <SlideText />
          </CardWrapper>
        </div>
        <div className="wrapper">
          <CardWrapper>
            <SlideText />
            <SlideText />
            <SlideText />
          </CardWrapper>
        </div>
        <div className="wrapper">
          <CardWrapper>
            <SlideText />
            <SlideText />
            <SlideText />
          </CardWrapper>
        </div>
      </Slider>
    );
  }
}
export default Slide;
