// src/Login.jsx
import React from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import Logo from "../../components/Logo/Logo";
import Button from "../../components/Button/Button";
import InputText from "../../components/Input/InputText";
import InputPW from "../../components/Input/InputPW";
import InputEmail from "../../components/Input/InputEmail";
import InputTel from "../../components/Input/InputTel";

const SingupWrap = styled.div`
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .input-wrap {
    width: 20%;
    input {
      margin-bottom: 1vh;
    }
  }
  .btn-wrap {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    button {
      :first-child {
        width: 65%;
      }
      :last-child {
        width: 32%;
      }
    }
  }
`;

const SignUp = () => {
  const navigate = useNavigate();
  let goHome = () => {
    navigate("/");
  };
  let goLogin = () => {
    navigate("/login");
  };
  let SubmitSign = (e) => {
    console.log(e);
    // navigate("/");
  };
  return (
    <SingupWrap>
      <Logo onClick={goHome} style={{ margin: "0 0 1rem 0" }}>
        Logo
      </Logo>
      <form className="input-wrap" autoComplete="off">
        <InputEmail
          placeholder=""
          id="userEmail"
          placeholder="Enter a gmail address"
          pattern=".+@gmail.com"
          sign
        ></InputEmail>
        <InputText placeholder="" id="userId" sign></InputText>
        <InputPW placeholder="" id="userPW" sign></InputPW>
        <InputPW placeholder=" " id="userPWCheck" sign></InputPW>
        <InputTel placeholder="" id="userPhoneNum" sign></InputTel>
        <div className="btn-wrap">
          <Button type="submit" info onClick={SubmitSign}>
            
          </Button>
          <Button cancel onClick={goLogin}>
            
          </Button>
        </div>
      </form>
    </SingupWrap>
  );
};

export default SignUp;
