import { BsChevronDoubleLeft } from "react-icons/bs";
import styled from "styled-components";
import Button from "../Button/Button";
import Regular from "../Text/Regular";
import Title from "../Text/Title";

const StyledUserProfile = styled.div`
  width: 40%;
  height: auto;
  display: flex;
  flex-direction: column;
  h2,
  p {
    text-align: left;
  }
  .reviseInput {
    display: none;
  }
  p {
    margin-bottom: 1.2vh;
  }
  .btn-wrap {
    margin-top: 2vh;
    display: flex;
    flex-direction: row;
    justify-content: left;
    Button {
      width: 45%;
      :first-child {
        width: 50%;
        margin-right: 1vw;
      }
      :last-child {
        width: 25%;
      }
    }
  }
`;

function UserProfile({ children, ...props }) {
  let userName = "";
  let userNickName = "";
  let userEmail = "";
  let userNum = "";
  let reviseInput = document.getElementsByClassName("reviseInput");

  function reviseProfile() {
    const getReviseInput = document.getElementsByClassName("reviseInput");
    const getP = document.getElementsByClassName("userProfile");
    const getCancelBtn = document.getElementById("cancelBtn");
    if (getCancelBtn.style.display == "none") {
      console.log("  ~");
      getCancelBtn.style.display = "block";
      for (let i = 0; i < 7; i++) {
        console.log(getP[i]);
        getP[i].style.display = "none";
        getReviseInput[i].style.display = "block";
      }
    } else {
      alert(" ~~");
      console.log("  !");
      getCancelBtn.style.display = "none";
      for (let i = 0; i < 7; i++) {
        let getI = getReviseInput[i];
        getP[i].style.display = "block";
        getP[i].innerText = getI.value;
        getReviseInput[i].style.display = "none";
      }
    }

    // getTest.style.color = 'red';
  }
  function cancel() {
    let getReviseInput = document.getElementsByClassName("reviseInput");
    let getP = document.getElementsByClassName("userProfile");
    let getCancelBtn = document.getElementById("cancelBtn");
    console.log("  !");
    getCancelBtn.style.display = "none";
    for (let i = 0; i < 7; i++) {
      getP[i].style.display = "block";
      getReviseInput[i].style.display = "none";
    }
  }
  return (
    <StyledUserProfile {...props}>
      <form
        onSubmit={function (e) {
          e.preventDefault();
        }}
      >
        <Title mypage></Title>
        <Regular id="UserName" className="userProfile">
          {props.name}
        </Regular>
        <input type="text" className="reviseInput"></input>

        <Title mypage></Title>
        <Regular id="UserNickName" className="userProfile">
          {props.nickname}
        </Regular>
        <input type="text" className="reviseInput"></input>

        <Title mypage></Title>
        <Regular id="UserEmail" className="userProfile">
          {props.email}
        </Regular>
        <input type="email" novalidate className="reviseInput"></input>

        <Title mypage></Title>
        <Regular id="UserPnum" className="userProfile">
          {props.num}
        </Regular>
        <input type="tel" className="reviseInput"></input>

        <div className="btn-wrap">
          <Button info onClick={reviseProfile}>
             
          </Button>
          <Button
            cancel
            id="cancelBtn"
            style={{ display: "none" }}
            onClick={cancel}
          >
            
          </Button>
        </div>
      </form>
    </StyledUserProfile>
  );
}

export default UserProfile;
