import "../../App.scss";
import styled, { ThemeConsumer } from "styled-components";
import { useNavigate, useRouteLoaderData } from "react-router-dom";
import Logo from "../../components/Logo/Logo";
import { useEffect } from "react";
import { get } from "jquery";

const LogoWrap = styled.div`
  width: 99vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 0.3rem;
  padding: 0 1.6rem;
  align-items: center;
`;
const LoginWrap = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const MenuText = styled.p`
  color: #191919;
  cursor: pointer;
  font-size: 0.5rem;
  margin-left: 1rem;
  border: none;
  background: none;
  :hover {
    color: #191919;
  }
  :nth-child(1) {
    display: block;
  }
  :nth-child(2) {
    // color: blue;
    display: none;
  }
`;
function Header() {
  const navigate = useNavigate();
  let goHome = () => {
    navigate("/");
  };

  let goMyPage = () => {
    if (window.localStorage.getItem("id")) {
      // 
      navigate("/mypage");
    } else if (window.localStorage.getItem("") == null) {
      alert(" !");
    }
    // console.log(window.localStorage.getItem("id"));
  };
  // 
  let goLogin = () => {
    navigate("/login");
  };
  // 
  function goLogout() {
    window.localStorage.clear();
    console.log(" !");
    navigate("/");
    document.getElementById("logout").style.display = "none";
    //login       !
    window.location.reload();
  }
  useEffect(() => {
    // console.log("useEffect~");
    if (window.localStorage.getItem("id", "nice")) {
      // 
      document.getElementById("logout").style.display = "block";
      document.getElementById("login").style.display = "none";
    } else if (window.localStorage.Key == "") {
      //  
      document.getElementById("login").style.color = "purple";
      document.getElementById("login").style.display = "none";
    }
  }, []);

  return (
    <LogoWrap>
      <Logo onClick={goHome}>Logo</Logo>
      <LoginWrap>
        <MenuText id="login" onClick={goLogin}>
          Log in
        </MenuText>
        <MenuText id="logout" onClick={goLogout}>
          Log out
        </MenuText>
        <MenuText id="mypage" onClick={goMyPage}>
          My Page
        </MenuText>
      </LoginWrap>
    </LogoWrap>
  );
}

export default Header;
