import React from "react";
import styled from "styled-components";
import Title from "../Text/Title";
import GrayText from "../Text/GrayText";
import Bold from "../Text/Bold";
import Review from "../Text/Regular";

const StyledEditorCard = styled.li`
  width:100%;
  height:auto;
  background:#fff;
  border:2px solid #9CD5C2;
  color: #9CD5C2;
  border-radius:0.6rem;
  font-size:0.6rem;
  padding: 0.5em 0.5rem;

  display:flex;
  flex-direction: column;
  cursor:pointer;
  }
`;


function EditorCard({ children,...props }) {
  function EditorClick(){
    alert('   !')
  }
  return(
    <StyledEditorCard {...props} onClick={EditorClick}>{children}
      <Title><Bold style={{marginBottom:'0.5rem'}}>Editor</Bold> </Title>
      
      <GrayText style={{marginBottom:'0.5rem'}}>  1 </GrayText>
      <Review style={{marginBottom:'0.5rem'}}>            .    ...</Review>
    </StyledEditorCard>
  );
}
export default EditorCard;
